.introbox {
  display: flex;
  align-items: center;
  gap: var(--gap-vertical);
}
@media only screen and (max-width: 850px) {
  .introbox{
    flex-direction: column;
    align-items: flex-start;
  }
}
.introbox h2 {
  color: var(--header);
}

.introbox p {
  color: var(--paragraph);
  font-size: 1.7rem;
  font-weight: 400;
  line-height: 1.6;
}

.introbox p span {
  font-weight: 400;
}

.introbox__image {
  height: 25rem;
  width: 25rem;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  cursor: pointer;
}

.introbox__image figcaption {
  position: absolute;
  bottom: 0%;
  right: 50%;
  transform: translateX(50%);
  font-size: 1.8rem;
  font-weight: bold;
  transition: all 0.3s ease-out;
  opacity: 0;
  text-transform: uppercase;
  /* background: rgba(0, 0, 0, 0.3);
    padding: 0.6rem;
    border-radius: 3px; */
}

.introbox__image img {
  transition: all 0.3s ease-out;
  height: 100%;
  width: 100%;
  transform: scale(1.1);
}

.introbox__image:hover img {
  transform: scale(1);
  filter: blur(3px) brightness(60%);
}

.introbox__image:hover img ~ figcaption {
  bottom: 50%;
  transform: translate(50%, 50%);
  opacity: 1;
}
