.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(4px);
  z-index: 1000;
  transition: all 0.5s;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--card);
  padding: 3.2rem 4rem;
  transition: all 0.5s;
  border-radius: 7px;

  width: 45rem;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.modal button {
  align-self: flex-end;
}

.modal h3 {
  font-size: 1.8rem;
  color: var(--heading);
}
.modal p {
  font-size: 1.4rem;
  color: var(--paragraph);
}
