.navbar {
  display: flex;
  position: fixed;
  z-index: 12;
  width: 100%;
}

.navbar ul {
  /* background-color: rgba(255, 255, 255, 0.5); */
  display: inline-flex;
  gap: 5rem;
  -webkit-backdrop-filter: saturate(200%) blur(30px);
  backdrop-filter: saturate(200%) blur(30px);
  background-color: rgba(35, 41, 48, 0.8) !important;

  padding: 2rem 10rem;
  margin: 0 auto;
  border-radius: 7px;
  margin-top: 2rem;
}

.navbar ul li {
  list-style: none;
}

.navbar ul li a {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  text-decoration: none;
  color: var(--paragraph);
  font-size: 1.6rem;
}
.navbar ul li a svg {
  filter: grayscale(100%);
}

.navbar ul li span {
  height: 20px;
  width: 20px;
}
.navbar ul li span svg {
  width: 100%;
  height: 100%;
  fill: var(--paragraph);
}

.navbar ul li a {
  text-decoration: none;
  color: var(--paragraph);
  font-size: 1.6rem;
}

@media only screen and (max-width: 850px) {
  .navbar ul {
    gap: 3rem;
    padding: 2rem 3rem;
  }
  .navbar ul li span {
    display: none;
  }
}

@media only screen and (max-width: 500px) {
  .navbar ul {
    gap: 5rem;
    padding: 2rem 3rem;
  }
  .navbar ul li span {
    display: block;
  }
  .navbar ul li p {
    display: none;
  }
}
