.hero {
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("../../public/bg.jpg");
  /* background: url("../../public/bg-3.jpg"); */
  position: relative;
  width: 100%;
  height: 85vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 5;
  background-size: cover;
  background-repeat: no-repeat;
}
.overview__icon {
  height: 100%;
  width: 100%;
}
.overview__icon svg {
  height: 100%;
  width: 100%;
}

