.hero .floating-box {
  position: absolute;
  object-fit: cover;
  /* background-color: #3e383b; */
  /* background-color: var(--card); */
  background-color: rgba(35, 41, 48, 0.8);

  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  transition: all 2.5s cubic-bezier(0.23, 0.9, 1, 1);
}

.floating-box--primary {
  height: 8.5rem;
  width: 8.5rem;
  z-index: 10;
}

.floating-box--secondary {
  height: 6.5rem;
  width: 6.5rem;
  z-index: 9;
}

.floating-box--outlined {
  height: 4.9rem !important;
  width: 4.9rem !important;
  background-color: transparent !important;
  border: 2px solid var(--card);
  z-index: 7;
}

.floating-box--empty {
  height: 5.4rem !important;
  width: 5.4rem !important;
  opacity: 0.7;
  z-index: 8;
}

