.form {
  /* background-image: linear-gradient(
        105deg,
        rgba(255, 255, 255, 0.9) 0%,
        rgba(255, 255, 255, 0.9) 50%,
        rgba(0, 0, 0, 0) 50%
      ), */
  /* url(./tech.jpg); */
  color: var(--paragraph);
  background-image: linear-gradient(
      105deg,
      rgba(38, 39, 44, 0.9) 0%,
      rgba(38, 39, 44, 0.9) 50%,
      rgba(0, 0, 0, 0) 50%
    ),
    url(./tech.jpg);
  background-size: cover;
  border-radius: 3px;
  background-repeat: no-repeat;
}

.contact__form {
  width: 50%;
  padding: 6rem;
  margin-top: 10rem;
}

input,
textarea {
  display: block;
  width: 80%;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid var(--paragraph);
  /* margin-bottom: 2rem; */
  padding: 1rem 0;
  box-sizing: content-box;
  transition: border-color 0.5s ease-in-out;
  transform-origin: left;
  color: var(--header);
  font-weight: normal;
  font-size: 1.5rem;
}
.input{
  margin-bottom: 2rem;
}
input:focus,
textarea:focus {
  border: none;
  outline: none;
  border-bottom: 2px solid var(--primary);
}

.error{
  font-weight: bold;
  color: var(--primary);
  font-size: 1.1rem;
}

.message {
  height: 100px;
}

.form h2 {
  margin-bottom: 5rem;
}

.form textarea {
  resize: none;
}

@media only screen and (max-width: 768px) {
  .form {
    background-image: linear-gradient(
        105deg,
        rgba(38, 39, 44, 0.9),
        rgba(38, 39, 44, 0.9)
      ),
      url(./tech.jpg);
  }

  .contact__form {
    width: 100%;
  }

  .contact__form input,
  .contact__form textarea {
    width: 100%;
  }
}
